

// loading state
.as--loading {
  cursor: default;
  position: relative;
  &:before {
    content:"";
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:rgba(255,255,255,.35) url(../img/common/loading.gif) no-repeat center;
    z-index: 2;
  }
}
