  @page {
    size: portrait 21cm 29.7cm;
    margin: 1cm;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
  .block-notice,
  #footer,
